/**
 *  @author 作者信息 [附属信息：如邮箱、日期]
 *  @param 参数名 {参数类型} 描述信息
 *  @example 示例代码
 *  @return {返回类型} 描述信息
 */

/**
 * @example this.pub_fun_scrollToTop()    // main.js引入了这个函数并挂载到实例上
 * @returns 平滑的滚顶到页面顶部
 */
export let scrollToTop = () => {   
    const c = document.documentElement.scrollTop || document.body.scrollTop;
    if (c > 0) {
        window.requestAnimationFrame(scrollToTop);
        window.scrollTo(0, c - c / 8);
    }
}

export default {
// --------------------验证类start--------------------
    /**
     * @param type { string } 要验证的字符串的类型 
     * @param str { string } 要验证的字符串
     * @example this.pub_fun.check_regular('phone','13190760326')
     * @returns { boolean } 正则，对应type验证对应str，通过返回true，反之返回false
     */
    check_regular : (type, str) => {
        switch (type){
            case 'idCard' : 
                return /^[1-9]\d{5}(?:18|19|20)\d{2}(?:0\d|10|11|12)(?:0[1-9]|[1-2]\d|30|31)\d{3}[\dXx]$/.test(str);     // 身份证号
            case 'phone' : 
                return /^[1][3,4,5,7,8][0-9]{9}$/.test(str);     // 手机号
            case 'email' : 
                return /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(str);     // 邮箱
            default:
                return false;     // 默认值，如果都不匹配，返回false
        }
    },
// --------------------验证类end--------------------





// --------------------操作类start--------------------
    /**
     * @param el { string } 要隐藏的元素选择器
     * @example this.pub_fun.hide_element('.token-bg / #xxx')
     * @returns 元素选择器对应元素隐藏
     */
    hide_element : (el) => {
        Array.from(document.querySelectorAll(el)).forEach(e => (e.style.display = 'none'));
    },

    /**
     * @example this.pub_fun.getScrollPosition()
     * @returns { object } 获取滚动位置，{ x: 0, y: 0 } 输出显示对应x轴，y轴位置
     */
    getScrollPosition : ( el = window ) => ({
        x: el.pageXOffset !== undefined ? el.pageXOffset : el.scrollLeft,
        y: el.pageYOffset !== undefined ? el.pageYOffset : el.scrollTop
    }),

    /**
     * @param str { string } 要复制到剪切板的字符串
     * @example this.pub_fun.copy_to_clipboard(参数)
     * @returns { string } 将参数复制到剪切板，粘贴即可使用
     */
    copy_to_clipboard : (str) => {     
        let Url2 = str;
        let oInput = document.createElement('input');
        oInput.value = Url2;
        document.body.appendChild(oInput);
        oInput.select();
        document.execCommand("Copy"); 
        oInput.className = 'oInput';
        oInput.style.display='none';
        alert('成功复制到剪切板')
    },

    /**
     * @param arr { array } 要排序的数组
     * @param type { nubmer } 1正序排序，2倒序排序，3随机排序，默认为正序
     * @example this.pub_fun.array_sort(数组)
     * @returns { object } 数组排序，输出返回排序完成后的数组
     */
    array_sort : (arr, type = 1) => {    
        return arr.sort((a, b) => {
            switch (type) {
                case 1:
                    return a - b;
                case 2:
                    return b - a;
                case 3:
                    return Math.random() - 0.5;
                default:
                    return arr;
            }
        })
    },

    /**
     * @param str { string } 要进行操作的字符串
     * @param type { number } 1首字母大写，2首字母小写，3大小写转换，4全部大写，5全部小写,默认转换全部小写
     * @example this.pub_fun.character_conversion(字符串，类型)
     * @returns { string } 字符串转换，返回转换后的字符串
     */
    character_conversion : (str, type = 5) => {   
        switch (type) {
            case 1:
                return str.replace(/\b\w+\b/g, function (word) {
                    return word.substring(0, 1).toUpperCase() + word.substring(1).toLowerCase();
    
                });
            case 2:
                return str.replace(/\b\w+\b/g, function (word) {
                    return word.substring(0, 1).toLowerCase() + word.substring(1).toUpperCase();
                });
            case 3:
                return str.split('').map(function (word) {
                    if (/[a-z]/.test(word)) {
                        return word.toUpperCase();
                    } else {
                        return word.toLowerCase()
                    }
                }).join('')
            case 4:
                return str.toUpperCase();
            case 5:
                return str.toLowerCase();
            default:
                return str;
        }
    },

    /**
     * @param num { number / string } 要进行转换的参数
     * @example this.pub_fun.keep_two_decimals(参数)
     * @returns { string } 保留两位小数，返回转换之后的字符串，如果没穿参数，返回‘参数为空’
     */
    keep_two_decimals : (num) => {
        num += ''
        if (num === 'undefined') {
            return '参数为空'
        } else if (num.indexOf(".") !== -1){
            return num.substr(0,num.indexOf(".")+3);
        } else {
            return num
        }
    },
// --------------------操作类end--------------------




// --------------------转换类start--------------------
    /**
     *  @param base64 { base64 } base64字符
     *  @param filename { string }  转换后的文件名
     *  @example this.pub_fun.base64_to_file(base64字符,转换后的文件名)
     *  @returns base64字符转换file文件，返回file文件
     */
    base64_to_file : (base64, filename )=> {
        let arr = base64.split(',');
        let mime = arr[0].match(/:(.*?);/)[1];
        let suffix = mime.split('/')[1] ;     // 图片后缀
        let bstr = atob(arr[1]);
        let n = bstr.length;
        let u8arr = new Uint8Array(n);
        while (n--) {
            u8arr[n] = bstr.charCodeAt(n)
        }
        return new File([u8arr], `${filename}.${suffix}`, { type: mime })
    },

    /**
     * @param blob { blob } 流文件
     * @param fileName { string } 文件名称
     * @example this.pub_fun.blob_to_file(blob流文件,转换后的文件名)
     * @returns blob流文件转换file文件，返回file文件
     */
    blob_to_file : (blob, fileName) => {
        blob.lastModifiedDate = new Date();
        blob.name = fileName;
        return blob;
    },

    /**
     * @param file { file } 图片文件
     * @example this.pub_fun.file_to_base64(文件)
     * @returns file文件转换base64字符，返回base64字符
     */
    file_to_base64 : (file) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (e) {
            return e.target.result
        };
    },

    /**
     * @param timestamp { number } 时间戳
     * @example this.pub_fun.timestamp_conversion_time(时间戳)
     * @returns { string } 时间戳转时间，如果传入时间戳，返回时间，精确到秒，否则提示无参
     */
    timestamp_conversion_time : ( timestamp ) => {
        if (timestamp) {
            timestamp = (timestamp + '').length <= 10 ? parseInt(timestamp + '000') : parseInt(timestamp);
            var time = new Date(timestamp);
            var year = time.getFullYear();
            var month = time.getMonth() + 1;
            var date = time.getDate();
            var hour = time.getHours();
            var minute = time.getMinutes();
            var second = time.getSeconds();
            month.toString().length < 2 && (month = '0' + month.toString());
            date.toString().length < 2 && (date = '0' + date.toString());
            hour.toString().length < 2 && (hour = '0' + hour.toString());
            minute.toString().length < 2 && (minute = '0' + minute.toString());
            second.toString().length < 2 && (second = '0' + second.toString());
    
            return year + "-" + month + "-" + date + " " + hour + ':' + minute + ':' + second;
        } else {
            return '参数为空'
        }
    },

    /**
     * @param stringTime { string } 时间
     * @example this.pub_fun.time_conversion_timestamp(时间)
     * @returns { number } 时间转时间戳，如果传入时间戳，返回时间，精确到秒，否则提示无参
     */
    time_conversion_timestamp : ( stringTime ) => {
        let timestamp = stringTime ? Date.parse(new Date(stringTime)) : 0;
        return timestamp / 1000; //2014-07-10 10:21:12的时间戳为：1404958872
    },
// --------------------转换类end--------------------





// --------------------判断类start--------------------
    /**
     * @example this.pub_fun.judgeDeviceType()
     * @returns { string } 判断当前设备，输出后，移动端设备返回Mobile，桌面端返回Desktop
     */
    judgeDeviceType : () => {
        return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ? 'Mobile' : 'Desktop';
    },

    /**
     * @param arr { array } 数组
     * @param val { string } 值
     * @example this.pub_fun.contains(数组,值)
     * @returns { boolean } 判断这个值是否在数组中，存在返回true，反之返回false
     */
    contains : (arr, val) => {     // 判断值是否在这个数组中
        return arr.indexOf(val) != -1 ? true : false;
    },

    /**
     * @param date_one { string } 第一个时间
     * @param date_two { string } 第二个时间
     * @example this.pub_fun.getDaysDiffBetweenDates(new Date('2017-12-13'), new Date('2017-12-22'))
     * @returns { number } 返回两个日期相差的天数
     */
    getDaysDiffBetweenDates : (date_one, date_two) => {
        let date_small = new Date(date_one)
        let date_big = new Date(date_two)
        date_small > date_big ? [date_small,date_big] = [date_big,date_small] : ''
        return (date_big - date_small) / (1000 * 3600 * 24);
    },
// --------------------判断类end--------------------
}