import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false

// 定义接口
import servicePlugin from './services/servicePlugin.js'
Vue.use(servicePlugin)

// 引入El-ui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI)

// 引入全局过滤器
import * as filters from './assets/js/filters'
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
})

// 引入全局css样式
import './assets/css/commonCss.less'

// 引入全局公共方法
import commonVariable from './assets/js/commonVariable'
import publicFunction from './assets/js/publicFunction'
import {scrollToTop} from './assets/js/publicFunction'     // 平滑滚动到顶部
Vue.prototype.pub_fun = publicFunction
Vue.prototype.pub_fun_scrollToTop = scrollToTop
Vue.use(commonVariable)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
