// 正则变量名.test(要验证的变量)
const idCard_regular = /^[1-9]\d{5}(?:18|19|20)\d{2}(?:0\d|10|11|12)(?:0[1-9]|[1-2]\d|30|31)\d{3}[\dXx]$/;    // 身份证号
const phone_regular = /^[1][3,4,5,7,8][0-9]{9}$/;    // 手机号
const email_regular = /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;    // 邮箱
const trains = /^[GCDZTSPKXLY1-9]\d{1,4}$/;     // 火车车次 
const Unified_social_credit_code = /^[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}$/;     // 统一社会信用代码
const bank_card = /^[1-9]\d{9,29}$/;     // 银行卡号
const zip_code = /^(0[1-7]|1[0-356]|2[0-7]|3[0-6]|4[0-7]|5[1-7]|6[1-7]|7[0-5]|8[013-6])\d{4}$/;     // 邮政编码（中国）

export default function(Vue) {
    Vue.prototype.$commonVariable = {
        idCard_regular,
        phone_regular,
        email_regular,
        trains,
        Unified_social_credit_code,
        bank_card,
        zip_code,
    }
}

